/* Base Flex setup */

.container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: #eee;
}

html, body, #root{
  padding:0px;
  margin:0px;
  width: 100vw;
  height: 100vh;
}

.header{
  width:100%;
  height: 60px;
  background-color: white;
}

.header-wrapper{
  position: relative;
  height: 80px; 

  /*header plus top bar*/
}

.horizontal-bar{
  width: 100%;
  background-color: purple;
  position: relative;
  height: 20px;
  -webkit-transition: height .25s linear;
  -o-transition: height .25s linear;
  transition: height .25s linear;
}

.main-content{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  background-color: peachpuff;
}

.bottom-bar{
  height: 100%;
  width: 100%;
  background-color: blue;
}

.vertical-bar {
  background-color: #eee;
  width: 100px;
  -webkit-transition: width .25s linear;
  -o-transition: width .25s linear;
  transition: width .25s linear;
}

.center{
  -ms-flex-positive: 1;
      flex-grow: 1;
  overflow-x: scroll;
}

/* Feed */

.feed-wrapper {
  width: 300px;
  height: calc(100vh - 128px);
  border: 1px solid #d2d4d7;
  margin-top: 15px;
  margin-left: 15px;
  -ms-flex: none;
      flex: none;
}

.feed-header {
  height: 50px;
  background-color: #fff;
  border-bottom: 2px solid #d2d4d7;
}

.feed-header span {
  padding-top: 5px;
  padding-left: 5px;
}

.feed-content {
  overflow-y: auto;
  height: calc(100vh - 180px);
}

feed-transition-wrapper {
  display: -ms-flexbox;
  display: flex;
}

/* Dock */
.dock{
  background-color: #ccc;
  border: 1px solid #d2d4d7;
  height: calc(100vh - 120px);
  width: 30px;
  position: absolute;
  top: 90px;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.doc-item{
  background: white;
  height: 40px;
  width: 100%;
  border-bottom: 1px solid #d2d4d7;
  padding: 5px 0px; 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}

/* Feed Card */

.feed-card {
  background-color: #fff;
  border-bottom: 1px solid #d2d4d7;
  min-height: 50px;
  padding: 5px;
  width: 290px;
}


/* Transtions */

.fade-enter {
  width: 0px;
  opacity: 0.01;
  -webkit-transition: all .5s linear;
  -o-transition: all .5s linear;
  transition: all .5s linear;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  width: 300px;
  -webkit-transition: all .5s linear;
  -o-transition: all .5s linear;
  transition: all .5s linear;
}

.fade-exit {
  opacity: 1;
  width: 300px;
}

.fade-exit.fade-exit-active {
  width: 0px;
  opacity: 0.01;
  -webkit-transition: all .5s linear;
  -o-transition: all .5s linear;
  transition: all .5s linear;
}



